const steps = [
  {
    element: '.home_title',
    popover: {
      title: 'logo',
      description: '',
      position: 'bottom',
      doneBtnText: '', // Text on the last button
      closeBtnText: '', // Text on the close button
      nextBtnText: '', // Next button text
      prevBtnText: '' // Previous button text
    }
  },
  {
    element: '.switchRole',
    popover: {
      title: '',
      description: '',
      position: 'left',
      doneBtnText: '', // Text on the last button
      closeBtnText: '', // Text on the close button
      nextBtnText: '', // Next button text
      prevBtnText: '' // Previous button text
    },
    padding: 0
  },
  {
    element: '.home-aside',
    popover: {
      title: '',
      description: '',
      position: 'right',
      doneBtnText: '', // Text on the last button
      closeBtnText: '', // Text on the close button
      nextBtnText: '', // Next button text
      prevBtnText: '' // Previous button text
    }
  }
  
]

export default steps



